body{
	font-family: "Montserrat Extra Light", sans-serif;
}

h3{
	color: $teal;
	text-transform: uppercase;
	text-align: center;
	font-size: 24px;
}

ul, li{
	list-style: none;
}

a {
	color: $teal;
	text-decoration: none;
	&:hover {
		color: $darkteal;
	}
}

/* paragraphs */

p {
	line-height: 1.5;
}

/*blockquotes*/

blockquote {
	background: #F7F7F7;
	margin: 0;
	padding: 20px;
	p {
		margin-top: 0;
	}
}

/*tables*/

table {
	td {
		padding: 10px;
	}
	tr td:first {
		padding-right: 0;
	}
}

/*def lists*/

dt {
	font-weight: 600;
	padding-bottom: 5px;
}

dd {
	padding: 10px;
	margin-left: 10px;
}

/*lists*/

ul li, ol li {
	padding: 3px;
}

ul {
	margin: 0;
	padding: 0;
}
/*PRE*/

pre {
	background: #F7F7F7;
	padding: 10px;
	border-radius: 4px;
	border: 1px solid #ccc;
}


/*
====================================
Global Classes / Wordpress Specific
====================================
*/

.aligncenter {
	text-align: center;
	margin: 0 auto;
}

.alignleft {
	float: left;
}

.alignright {
	float: right;
}

img {
	&.alignleft {
		padding-right: 5px;
	}
	&.alignright {
		padding-left: 5px;
	}
}

.clear {
	width: 100%;
	float: left;
	clear: both;
}

::-webkit-input-placeholder {
    font-family: "Montserrat Extra Light";
}
:-moz-placeholder {
    font-family: "Montserrat Extra Light";
}
::-moz-placeholder {
    font-family: "Montserrat Extra Light";
}
:-ms-input-placeholder {
    font-family: "Montserrat Extra Light";
}